html {
	font-size: 62.5%;
}

body {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
}

.nav-list {
	height: 10vh;
	display: flex;
	width: 50%;
	justify-content: space-around;
	align-items: center;
	margin-left: auto;
	list-style: none;
}

.nav-links {
	text-decoration: none;
	color: black;
	font-size: 1.8rem;
}

.home,
.about {
	height: 90vh;
	position: absolute;
	width: 100%;
}

.home-content,
.about-content {
	height: 100%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;

	img {
		height: 70%;
	}

	h1 {
		font-size: 5rem;
		margin: 3rem 0;
	}

	p {
		font-size: 2rem;
	}
}

.home {
	background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.about {
	background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}
